}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.equaliser-container {
  height: 30px;
  width: 40px;
  margin: 0;
  padding: 0;
  position: relative;
}
.colour-bar {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  height: 30px;
  background: #999797;
}
.equaliser-column {
  width: 2px;
  float: left;
  margin: 0 1px 0 0;
  padding: 0;
  height: 30px;
  position: relative;
  list-style-type: none;
}
.equaliser-column:first-child .colour-bar {
  -webkit-animation: color-bar 2s ease-out 1s infinite alternate;
  animation: color-bar 2s ease-out 1s infinite alternate;
}
.equaliser-column:nth-child(2) .colour-bar {
  -webkit-animation: color-bar 2s ease-out 0.5s infinite alternate;
  animation: color-bar 2s ease-out 0.5s infinite alternate;
}
.equaliser-column:nth-child(3) .colour-bar {
  -webkit-animation: color-bar 2s ease-out 1.5s infinite alternate;
  animation: color-bar 2s ease-out 1.5s infinite alternate;
}
.equaliser-column:nth-child(4) .colour-bar {
  -webkit-animation: color-bar 2s ease-out 0.25s infinite alternate;
  animation: color-bar 2s ease-out 0.25s infinite alternate;
}
.equaliser-column:nth-child(5) .colour-bar {
  -webkit-animation: color-bar 2s ease-out 0.6s infinite alternate;
  animation: color-bar 2s ease-out 0.6s infinite alternate;
}
.equaliser-column:nth-child(6) .colour-bar {
  -webkit-animation: color-bar 2s ease-out 0.8s infinite alternate;
  animation: color-bar 2s ease-out 0.8s infinite alternate;
}
.equaliser-column:nth-child(7) .colour-bar {
  -webkit-animation: color-bar 2s ease-out 1s infinite alternate;
  animation: color-bar 2s ease-out 1s infinite alternate;
}
.equaliser-column:nth-child(8) .colour-bar {
  -webkit-animation: color-bar 2s ease-out 0.5s infinite alternate;
  animation: color-bar 2s ease-out 0.5s infinite alternate;
}
.equaliser-column:nth-child(9) .colour-bar {
  -webkit-animation: color-bar 2s ease-out 0.3s infinite alternate;
  animation: color-bar 2s ease-out 0.3s infinite alternate;
}
.equaliser-column:nth-child(10) .colour-bar {
  -webkit-animation: color-bar 2s ease-out 1s infinite alternate;
  animation: color-bar 2s ease-out 1s infinite alternate;
}

.equaliser-column:nth-child(10) .colour-bar {
  -webkit-animation: color-bar 2s ease-out 1s infinite alternate;
  animation: color-bar 2s ease-out 1s infinite alternate;
}
.equaliser-column:last-child {
  margin-right: 0;
}

@keyframes color-bar {
  0% {
    height: 10px;
    background: #999797;
  }
  10% {
    height: 17px;
    background: #999797;
  }
  20% {
    height: 15px;
    background: #999797;
  }
  30% {
    height: 14px;
    background: #999797;
  }
  40% {
    height: 10px;
    background: #999797;
  }
  50% {
    height: 13px;
    background: #999797;
  }
  60% {
    height: 10px;
    background: #999797;
  }
  70% {
    height: 18px;
    background: #999797;
  }
  80% {
    height: 20px;
    background: #999797;
  }
  90% {
    height: 14px;
    background: #999797;
  }
  to {
    height: 6px;
    background: #999797;
  }
}
.App {
  text-align: center;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin 20s linear infinite;
    animation: App-logo-spin 20s linear infinite;
  }
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #fff;
}
.App-link {
  color: #61dafb;
}
@-webkit-keyframes App-logo-spin {
  0% {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(1turn);
  }
}
@keyframes App-logo-spin {
  0% {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(1turn);
  }
}
